import {globalCamera, globalObject} from "./simple-3d";
import {Vector3} from "three";

const scale = 50.;

export function orthogonalView(meshName) {
    for (const faceObject of globalObject.faces) {
        if (faceObject.name === meshName) {
            globalCamera.position.set(
                faceObject.planeValues.a * scale,
                faceObject.planeValues.c * scale,
                -faceObject.planeValues.b * scale
            );

            globalCamera.lookAt(new Vector3(0.,0.,0.));
        }
    }
}