import {
    BufferAttribute,
    BufferGeometry,
    Mesh, MeshBasicMaterial,
    MeshLambertMaterial,
    MeshPhongMaterial,
    MeshStandardMaterial, Vector3
} from "three";
import {getRandomColor, newBasicMaterial} from "./material-utils";
import {globalObject} from "../viewModes/simple-3d";


const DIVISIONS = 10;


function mappingCoordinatesFromCAD(cadXYZ) {
    return [cadXYZ[0], cadXYZ[2], -cadXYZ[1]];
}


function faceList4Simple(faceIndexes, vertices) {
    let vertexList = [];

    const fA = [faceIndexes[0], faceIndexes[1], faceIndexes[2]];
    const fB = [faceIndexes[0], faceIndexes[2], faceIndexes[3]];

    for (const fIs of [fA, fB]) {
        for(const i of fIs) {
            for (const v of mappingCoordinatesFromCAD(vertices[i])){
                vertexList.push(v);
            }
        }
    }

    return vertexList;
}

function faceList4Division(faceIndexes, vertices, divisions = 2) {
    const v_a = mappingCoordinatesFromCAD(vertices[faceIndexes[0]]);
    const v_b = mappingCoordinatesFromCAD(vertices[faceIndexes[1]]);
    const v_c = mappingCoordinatesFromCAD(vertices[faceIndexes[2]]);
    const v_d = mappingCoordinatesFromCAD(vertices[faceIndexes[3]]);

    const v0base = new Vector3(v_a[0], v_a[1], v_a[2]);
    const v1base = new Vector3(v_b[0], v_b[1], v_b[2]);

    const v0 = new Vector3(
        v_d[0]-v_a[0],
        v_d[1]-v_a[1],
        v_d[2]-v_a[2]
    );

    const v1 = new Vector3(
        v_c[0]-v_b[0],
        v_c[1]-v_b[1],
        v_c[2]-v_b[2]
    );

    let tParameters = [];
    const delta = 1. / divisions;

    for (let i = 0; i < (divisions + 1); i++) {
        tParameters.push(i * delta);
    }

    let grid = [];

    // console.log("tParameters: ", tParameters);

    for (const t0 of tParameters) {
        const locV0 = new Vector3().addVectors(v0base,
            new Vector3().addScaledVector(v0, t0));
        const locV1 = new Vector3().addVectors(v1base,
            new Vector3().addScaledVector(v1, t0));

        const locV = new Vector3().subVectors(locV1, locV0);

        let gridRow = [];

        for (const t1 of tParameters) {
            gridRow.push(new Vector3().addVectors(locV0,
                new Vector3().addScaledVector(locV, t1))
            );
        }

        grid.push(gridRow);
    }

    let vertexList = [];

    for (let i = 0; i < divisions; i++) {
        for (let j = 0; j < divisions; j++) {
            const vs = [
                grid[i][j],
                grid[i][j+1],
                grid[i+1][j+1],
                grid[i+1][j]
            ]

            for (const vIs of [[0, 1, 2],[0, 2, 3]]) {
                for (const vI of vIs) {
                    const v = vs[vI];
                    vertexList.push(v.x);
                    vertexList.push(v.y);
                    vertexList.push(v.z);
                }
            }
        }
    }

    // console.log("vertexList: ", vertexList);

    return vertexList;
}


function positionFaceListFromFace(faceIndexes, vertices, divisions = 3) {
    let vertexList = [];

    if (faceIndexes.length === 3) {
        for(const i of faceIndexes) {
            vertexList.concat(vertices[i]);
        }
    } else if (faceIndexes.length === 4) {
        if (divisions > 1) {
            vertexList = faceList4Division(faceIndexes, vertices, DIVISIONS);
        } else {
            vertexList = faceList4Simple(faceIndexes, vertices);
        }
    }

    return vertexList;
}

function bufferGeometryFromFace(faceIndexes, vertices) {
    const geometry = new BufferGeometry();

    const vertexList = positionFaceListFromFace(faceIndexes, vertices);

    const bufferPosition = new Float32Array(vertexList);

    geometry.setAttribute( 'position', new BufferAttribute( bufferPosition, 3 ) );
    geometry.computeVertexNormals();

    return geometry;
}

function meshFromFace(faceIndexes, vertices, material, name) {
    const mesh = new Mesh(
        bufferGeometryFromFace(faceIndexes, vertices),
        material
    );

    if (name !== null) {
        mesh.name = name;
    }

    return mesh;
}

function getName(faceJsonObject) {
    let name;
    if ("name" in faceJsonObject) {
        name = faceJsonObject["name"];
    } else {
        name = null;
    }

    return name;
}

export function parseJSONToBufferGeometry(jsonObject) {
    const geometry = new BufferGeometry();
    let vertexList = [];

    for (const face of jsonObject["faces"]) {
        const locVertexList = positionFaceListFromFace(face["indexes"], jsonObject["vertices"]);
        vertexList.push.apply(vertexList, locVertexList);
    }

    const bufferPosition = new Float32Array(vertexList);

    geometry.setAttribute( 'position', new BufferAttribute( bufferPosition, 3 ) );
    geometry.computeVertexNormals();

    return geometry;
}

const NAME_MAP = {
    'F': "walls",
    'R': "roofs",
    null: "other"
}

function constructEmptyMesh() {
    const cleanMesh = {}

    for (const [_, value] of Object.entries(NAME_MAP)) {
        cleanMesh[value] = [];
    }

    return cleanMesh;
}

function mapNameToType(mesh) {
    if (mesh.name === null) {
        return NAME_MAP[null];
    } else {
        const char = mesh.name[0];
        if (char in NAME_MAP) {
            return NAME_MAP[char];
        } else {
            return NAME_MAP[null];
        }
    }
}

export function parseJSONToMeshes(jsonObject, color = null) {
    let mesh = constructEmptyMesh();

    if (color !== null) {
        const material = new MeshBasicMaterial( { color: color } );
        for (const face of jsonObject["faces"]) {
            const locMesh = meshFromFace(face["indexes"], jsonObject["vertices"], material, getName(face));

            mesh[mapNameToType(locMesh)].push(locMesh);
        }
    } else {
        for (const face of jsonObject["faces"]) {
            const material = newBasicMaterial(null, .7);

            const locMesh = meshFromFace(face["indexes"], jsonObject["vertices"], material, getName(face));

            mesh[mapNameToType(locMesh)].push(locMesh);
        }
    }

    return mesh;
}
