import {Mesh, MeshBasicMaterial, Raycaster, Scene, Vector3} from 'three';
import {getPerspectiveRenderer, resize} from "./renderers";
import {getPerspectiveCamera} from "./cameras";
import {getOrbitControls} from "./orbit-controls";
import {start, stop} from './render-loop';
import {addCubeToScene, loadObj} from "../testFunctions/add-geo-to-scene";
import {
    SAMPLE_DEF_ZH,
    sampleBerlinGeometry,
    sampleGeometry,
    sampleZHGeometry,
    sampleZHMeshes
} from "../testFunctions/prototype-object";
import {meshWithTextureForBufferGeo, simpleBoxWithTexture} from "../testFunctions/test-texture-map";
import {OutlineGeometry} from "../meshManager/outline-geometry";

const OUTLINE_MATERIAL = new MeshBasicMaterial(
    {
        color: 0xf9d949,
        opacity: 0.8
    }
);

export function setUp(mount) {

    const scene = new Scene();

    const meshes = sampleZHMeshes();

    console.log(meshes);

    const singleGeo = sampleZHGeometry();
    const singleMesh = new Mesh(singleGeo, OUTLINE_MATERIAL);

    let translationSet = {x: 0, y: 0, z: 0};
    const startMesh = meshWithTextureForBufferGeo(singleGeo, translationSet);

    for (const mesh of meshes.roofs){
        mesh.geometry.translate(translationSet.x, translationSet.y, translationSet.z);
        scene.add(mesh);
    }
    for (const mesh of meshes.walls){
        mesh.geometry.translate(translationSet.x, translationSet.y, translationSet.z);
        scene.add(mesh);
    }

    const outlineMesh = OutlineGeometry(singleMesh);
    outlineMesh.name = 'outline';
    scene.add(outlineMesh);

    const renderer = getPerspectiveRenderer();
    mount.current.appendChild(renderer.domElement);

    const camera = getPerspectiveCamera(mount.current);
    scene.add(camera);

    const orbitControls = getOrbitControls(camera, renderer);

    resize(mount, renderer, camera);
    const frameId = start(renderer, scene, camera);

    return {
        scene: scene,
        renderer: renderer,
        camera: camera,
        frameId: frameId,
        orbitControls: orbitControls,
        jsonObject: SAMPLE_DEF_ZH
    };
}

export function cleanUp(mount, renderer, frameId) {
    stop(frameId.frameId);
    mount.current.removeChild(renderer.domElement);
}
