import {BackSide, Mesh, MeshBasicMaterial} from "three";

export function OutlineGeometry(mesh) {
    const bufferGeometry = mesh.geometry;

    const outlineMat = new MeshBasicMaterial( { color: 0xFFCC00, side: BackSide, transparent: true } );
    const outlineMesh = new Mesh( bufferGeometry, outlineMat );
    outlineMesh.position.set(
        mesh.position.x,
        mesh.position.y,
        mesh.position.z
    );
    outlineMesh.scale.multiplyScalar(1.02);

    return outlineMesh;
}
