import React, {useEffect, useRef} from 'react';
import Measure from 'react-measure';

import {resize} from '../../threeComponents/common/renderers';
import {cleanUp, setUp} from '../../threeComponents/common/simple-set-up';

import './simple-3d.scss';
import {textureMapping} from "../testFunctions/test-texture-map";
import {exportSceneAsGLTF, exportSceneAsUSDZ} from "../meshManager/simple-exporters";
import {addCubeToScene} from "../testFunctions/add-geo-to-scene";

export let globalScene;
export let globalCamera;
export let globalRenderer;
export let globalObject;

function Simple3d() {
    const mount = useRef(null);
    const rendererRef = useRef(null);
    const cameraRef = useRef(null);

    useEffect(() => {
        const {scene, camera, renderer, frameId, jsonObject} = setUp(mount);

        globalScene = scene;
        globalCamera = camera;
        globalRenderer = renderer;
        globalObject = jsonObject;

        exportSceneAsGLTF('gltf_file', scene);
        exportSceneAsUSDZ('usdz_file', scene);

        rendererRef.current = globalRenderer;
        cameraRef.current = globalCamera;

        return () => {
            cleanUp(mount, renderer, frameId);
        }
    }, [rendererRef, cameraRef]);

    return (
        <Measure
            bounds
            onResize={() => {
                resize(mount, rendererRef.current, cameraRef.current)
            }}>
            {({measureRef}) => (
                <div className={'simple-3d'} ref={measureRef}>
                    <div className={'canvas-container'} ref={mount}/>
                </div>
            )}
        </Measure>
    )
}

export default Simple3d;
