import {MeshBasicMaterial} from "three";

export function getRandomColor() {
    const lettersA = '0123456789abcdef';
    const lettersB = '56789abcdef';
    let color = '#';
    for (let i = 0; i < 3; i++) {
        color += lettersA[Math.floor(Math.random() * lettersA.length)];
        color += lettersB[Math.floor(Math.random() * lettersB.length)];
    }
    return color;
}

export function newBasicMaterial(color = null, opacity = 1.0) {
    if (color === null) {
        color = getRandomColor();
    }

    return new MeshBasicMaterial({color: color, opacity: opacity});
}