import {BoxGeometry, BufferAttribute, Mesh, MeshBasicMaterial, Texture, TextureLoader, Vector3} from 'three';

import {globalScene, globalCamera} from "../viewModes/simple-3d";
import {getRandomColor, newBasicMaterial} from "../meshManager/material-utils";
import {shaderGrid} from "../meshManager/shaders";
// import a_building from "../../../src/data/images/a_building.png";

export const MAPPING_IMAGE_URL = "https://i.ibb.co/vL3kCcV/a-building.png";
// export const MAPPING_IMAGE_URL = "https://i.ibb.co/T89mytQ/ML-Strasse-105-Berlin.jpg";

// export let mesh;

const ACTIVE_FACES = ["F9", "F10", "F11"];
const STATIC_FACES = ['outline'];

export function textureMapping() {
    const newTextureMaterial = textureMaterial();

    // console.log(newTextureMaterial);

    for (const sceneObject of globalScene.children) {
        // console.log(sceneObject);
        if (sceneObject.type === "Mesh" & ACTIVE_FACES.includes(sceneObject.name) ) {
            textureMappingSingleMesh(sceneObject, newTextureMaterial);
        }
    }
}

export function resetAllTextures() {
    console.log("resetting all textures");
    for (const sceneObject of globalScene.children) {
        if (sceneObject.type === "Mesh" & ACTIVE_FACES.includes(sceneObject.name) ){
            sceneObject.material = shaderGrid();
        }

        else if (sceneObject.type === "Mesh" & !STATIC_FACES.includes(sceneObject.name) ) {
            sceneObject.material = newBasicMaterial(getRandomColor(), .7);
        }
    }
}

export function textureMappingSingleMesh(mesh, textureMaterial = null) {
    let uvs = [];

    if (textureMaterial !== null) {
        mesh.material = textureMaterial;
    }

    console.log(mesh)

    for (let i = 0; i < mesh.geometry.attributes.position.count; i++) {
        // console.log(mesh.geometry.attributes.position.array);
        const coordinate = new Vector3().fromArray(mesh.geometry.attributes.position.array, i * 3);
        coordinate.project(globalCamera);

        uvs.push(coordinate.x * .5 + .5);
        uvs.push(coordinate.y * .5 + .5);
    }

    const uvPositions = new Float32Array(uvs);

    mesh.geometry.setAttribute( 'uv', new BufferAttribute( uvPositions, 2 ) );

    console.log(mesh.geometry);
    mesh.needsUpdate = true;
}

function textureMaterial() {
    const loader = new TextureLoader();

    return new MeshBasicMaterial( {
        map: loader.load(MAPPING_IMAGE_URL),
        transparent: true,
        opacity: 0.85,
    });
}

export function meshWithTextureForBufferGeo(bufferGeo, translation) {
    const material = textureMaterial();

    bufferGeo.computeBoundingSphere();

    translation.x = -bufferGeo.boundingSphere.center.x;
    translation.y = -bufferGeo.boundingSphere.center.y;
    translation.z = -bufferGeo.boundingSphere.center.z;

    bufferGeo.translate(
        -bufferGeo.boundingSphere.center.x,
        -bufferGeo.boundingSphere.center.y,
        -bufferGeo.boundingSphere.center.z,
    )

    return new Mesh(bufferGeo, material);
}

export function simpleBoxWithTexture() {
    const loader = new TextureLoader();

    const material = new MeshBasicMaterial( {
        map: loader.load(MAPPING_IMAGE_URL),
        transparent: true,
        opacity: 0.8,
    })

    const boxGeo = new BoxGeometry(1, 1, 1, 5, 5, 5);

    return new Mesh(boxGeo, material);
}
